@include font('Arboria-Book', Arboria-Book);
@include font('Arboria-Medium', Arboria-Medium);

@mixin reg {
	font-family: 'Arboria-Book', Arial, Helvetica, sans-serif; }

@mixin med {
	font-family: 'Arboria-Medium', Arial, Helvetica, sans-serif; }

@mixin pop {
	font-family: 'Poppins', Arial, Helvetica, sans-serif; }

h2, .h2 {
	color: #fff;
	text-transform: uppercase;
	@include reg;
	font-weight: normal;
	font-size: 30px;
	letter-spacing: .04em; }

h3, .h3 {
	color: #fff;
	@include reg;
	font-weight: normal;
	font-size: 24px;
	letter-spacing: 1px; }

.title {
	padding-bottom: 5px;
	border-bottom: 2px solid #0e222e; }

.label {
	display: inline-block;
	padding: 6px 6px 3px;
	border-radius: 4px;
	background: #42BFFE;
	color: #fff;
	@include pop;
	font-weight: 300;
	text-transform: uppercase;
	font-size: 14px;
	line-height: 1;
	letter-spacing: .05em; }

.text-right {
	text-align: right; }

.meta {
	@include pop;
	font-size: 12px;
	color: #9fa2a5; }

.link {
	display: inline-block;
	vertical-align: top;
	text-transform: uppercase;
	font-size: 15px;
	letter-spacing: .06em;
	color: #fff;
	transition: color .3s;
	+ .link {
		margin-left: 20px; }
	&.is-active, &:hover {
		color: $c-blue; }
	.icon {
		fill: currentColor;
		margin-right: 4px;
		display: inline-block;
		vertical-align: middle; }
	.icon-delete {
		position: relative;
		top: -2px;
		font-size: 16px; }
	span {
		display: inline-block;
		vertical-align: middle;
		line-height: 1; } }

.link-more {
	text-transform: uppercase;
	font-size: 15px;
	letter-spacing: 0.04em;
	color: #fff;
	transition: color .3s;
	&:hover {
		color: $c-blue; }
	.icon {
		font-size: 3px;
		fill: currentColor;
		margin-left: 6px;
		vertical-align: middle;
		position: relative;
		top: -1px; } }

.list {
	&__title {
		@include pop;
		font-weight: 300;
		font-size: 14px;
		color: #9fa2a5;
		margin-bottom: 5px; }
	&__info {
		&:not(:last-child) {
			margin-bottom: 18px; } }
	&__item {
		@include pop;
		font-size: 16px;
		&:not(:last-child) {
			margin-bottom: 3px; } } }
