.info {
	$this: &;

	position: relative;
	@include pop;
	&__channel {
		position: absolute;
		left: 0;
		top: 0;
		width: 70px;
		min-width: 70px;
		img {
			max-width: 100%;
			height: auto; } }
	&__body {
		padding-top: 6px;
		padding-left: 100px; }
	&__heading {
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 23px; }
	&__title {
		display: block;
		margin-bottom: 7px;
		color: #fff;
		font-size: 20px;
		&:link {
			transition: color .3s;
			&:hover {
				color: $c-blue; } } }
	&__subtitle {
		color: rgba(#fff, .6); }
	&__text {
		position: relative;
		min-height: 105px;
		padding-bottom: 22px;
		color: rgba(#fff, .6);
		font-size: 14px;
		line-height: 1.5;
		&:not(:last-child) {
			margin-bottom: 15px; }
		&:not(.is-truncuted) {
			#{$this}__more {
				display: none; } }
		&.is-open {
			#{$this}__more {
				transform: rotateX(180deg); } }
		h5 {
			margin-bottom: 5px;
			font-weight: 300;
			color: #fff;
			font-size: inherit;
			letter-spacing: .06em; } }
	&__about {
			position: relative;
			height: 63px;
			overflow: hidden;
			@include gpu;
			.clone {
				display: none; } }
	&__more {
			position: absolute;
			bottom: 0;
			left: 50%;
			margin-left: -15px;
			width: 30px;
			height: 20px;
			background: none url(data:image/svg+xml;base64,) center center;
			background-position: contain;
			transition: transform .5s; }
	&__progress {
		margin-bottom: 23px; }
	&__buttons {
		.button {
			min-width: 204px;
			&:not(:last-child) {
				margin-right: 25px; } } } }

.info_simple {
	.info {
		&__top {
			display: flex;
			margin-bottom: 50px; }
		&__body {
			flex: 100%; }
		&__text {
			min-height: 0;
			padding-bottom: 0;
			margin-bottom: 0; }
		&__progress {
			margin-bottom: 0; } } }
