.movie_links { padding-bottom: 50px; }

.modal { position: fixed; top: 0; left: 0; width: 100%; min-width: 1280px; height: 100vh; padding: 15px; background: rgba(0, 0, 0, 0.2); overflow: auto; z-index: 999; display: none; text-align: center; font-size: 0; }

.modal:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.modal__inner { display: inline-block; vertical-align: middle; width: 703px; font-size: 14px; text-align: left; }

.modal__top, .modal__body { padding-left: 60px; padding-right: 60px; }

.modal__top { position: relative; padding-bottom: 30px; margin-bottom: 40px; border-bottom: 1px solid rgba(255, 255, 255, 0.6); }

.modal__img { height: 160px; border-radius: 4px; background-size: cover; background-position: center; }

.modal__title { font-size: 24px; letter-spacing: .06em; }

.modal__close { position: absolute; top: -7px; right: 24px; }

.modal__row { margin: 0 -17px; }

.modal__row:after { content: " "; display: table; clear: both; }

.modal__col { float: left; width: 50%; padding: 0 17px; }

.modal__body { font-family: 'Poppins', Arial, Helvetica, sans-serif; }

.modal__main { margin-bottom: 40px; }

.modal__heading { margin-bottom: 40px; padding-top: 6px; }

.modal__name { font-family: 'Poppins', Arial, Helvetica, sans-serif; font-size: 20px; font-weight: 500; margin-bottom: 10px; letter-spacing: .03em; }

.modal__subtitle { letter-spacing: .08em; text-transform: uppercase; }

.modal__price { font-weight: 500; font-size: 18px; margin-bottom: 7px; }

.modal__meta { font-family: 'Poppins', Arial, Helvetica, sans-serif; font-size: 12px; line-height: 1.33333; color: #a2a4a6; }

.modal__meta_info { margin-top: 7px; text-align: right; }

.change { text-align: right; }

.change.is-active .change__final { display: block; }

.change.is-active .change__edit { display: none; }

.change__final { display: none; }

.empty { text-align: center; font-family: 'Poppins', Arial, Helvetica, sans-serif; font-weight: 500; font-size: 18px; color: #92979b; }

.empty__icon { margin-bottom: 15px; }

.empty__icon .icon { font-size: 48px; fill: currentColor; vertical-align: top; }

.empty__text { max-width: 230px; margin: 0 auto; }

.login { display: flex; width: 100%; }

.login__col { flex: 1; }

.login__col_form { padding: 40px 40px 80px; }

.login__img { position: relative; height: 100%; background-size: cover; background-position: center; }

.login__img:after { content: ''; position: absolute; left: 0; right: 0; width: 100%; height: 100%; background: rgba(15, 29, 43, 0.5); }

.login__container { display: flex; position: relative; max-width: 560px; margin: 0 auto; flex-direction: column; height: 100%; }

.login__top { display: flex; justify-content: space-between; align-items: center; }

.login__form { width: 380px; margin: auto; }

.table { font-family: 'Poppins', Arial, Helvetica, sans-serif; font-size: 14px; }

.table__row { display: flex; min-height: 60px; align-items: center; }

.table__edit { margin-left: auto; }

.table__header .table__row, .table__body .table__row { padding-left: 40px; }

.table__header { border-bottom: 2px solid #0e222e; }

.table__header .table__row { min-height: 0; padding-bottom: 15px; }

.table__header .table__cell { text-transform: uppercase; }

.table__body { font-size: 16px; }

.table__body .table__row { padding-right: 40px; }

.table__body .table__row:nth-child(odd) { background: rgba(0, 0, 0, 0.2); }

.table__body .table__edit { opacity: 0; visibility: hidden; transition: opacity .3s, visibility .3s; }

.table__cell:not(:last-child) { margin-right: 40px; }

.table__id { flex: 0 0 115px; }

.table__type { flex: 0 0 150px; }

.table__start { flex: 0 0 115px; }

.table__end { flex: 0 0 140px; }

.table__row.is-removable .table__edit { opacity: 1; visibility: visible; }

.table__profile { flex: 0 0 112px; }

.table__profile .icon { font-size: 25px; }

.table__name { flex: 0 0 155px; }
