.chapter {
	&:not(:last-child) {
		margin-bottom: 62px; }
	&__gallery {
		&:not(.slick-initialized) {
			white-space: nowrap;
			overflow: hidden;
			.item {
				white-space: normal; } } }
	.slick-slide {
		padding: 0 15px; }
	.slick-list {
		margin: 0 -15px; }
	.slick-arrow {
		position: absolute;
		width: 45px;
		height: 45px;
		top: 85px;
		z-index: 2;
		transition: opacity .3s;
		&.slick-disabled {
			opacity: 0; } }
	.slick-prev {
		left: -24px;
		background-image: url(data:image/svg+xml;base64,); }
	.slick-next {
		right: -26px;
		background-image: url(data:image/svg+xml;base64,); }
	&__title {
		margin-bottom: 30px;
		display: flex;
		justify-content: space-between;
		align-items: flex-end; }
	&__change {
		line-height: 1.4; }
	&__slide {
		display: inline-block;
		vertical-align: top;
		&:not(:last-child) {
			margin-right: 30px; } } }

.chapter_sm {
	&:not(:last-child) {
		margin-bottom: 50px; } }


.item {
	$this: &;

	display: block;
	min-width: 276px;
	padding-bottom: 5px;
	&.is-removable {
		#{$this}__delete {
			display: flex; } }
	&__top {
		position: relative;
		height: 210px;
		margin-bottom: 20px;
		border-radius: 4px;
		overflow: hidden; }
	&__img {
		display: block;
		height: 100%;
		background-size: cover;
		background-position: center;
		&:hover {
			#{$this} {
				&__play {
					opacity: 1; } } } }
	&__play {
		border-radius: 0; }
	&__name {
		display: inline;
		color: #fff;
		@include pop;
		font-size: 16px;
		transition: color .3s;
		cursor: pointer;
		&:after {
			content: '>';
			color: inherit;
			display: inline-block;
			vertical-align: middle;
			position: relative;
			top: 1px;
			width: 10px;
			margin-left: 10px; }
		&:hover {
			color: $c-blue;
			span {
				text-decoration: underline; } }
		span {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			max-width: 90%;
			max-width: calc(100% - 25px);
			color: inherit;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis; } }
	&__logo {
		position: absolute;
		top: 20px;
		right: 20px;
		width: 100px;
		z-index: 1;
		img {
			max-width: 100%;
			height: auto; } }
	&__info {
		display: flex;
		align-items: center;
		min-height: 93px;
		margin-top: 20px;
		padding: 14px 30px;
		border-radius: 3px;
		background: #000;
		border: 1px solid #081c26; }
	&__text {
		@include pop;
		font-size: 12px;
		font-weight: 300;
		color: #99999a;
		line-height: 1.4;
		p {
			&:not(:last-child) {
				margin-bottom: 7px; } }
		strong {
			display: block;
			font-size: 18px;
			font-weight: 500;
			color: #fff;
			letter-spacing: 0.06em;
			line-height: 1;
			margin-bottom: 7px; } }
	&__delete {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: none;
		align-items: center;
		justify-content: center;
		background: rgba(#0F1D2B , .9);
		z-index: 99;
		@include gpu;
		.icon {
			fill: #fff;
			font-size: 40px; } } }

.item_mod {
	.item {
		&__img {
			&:hover {
				&:after {
					opacity: 0; } }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: inherit;
				background: rgba(#143D45, .4);
				transition: opacity .3s; } } } }

.c-item {
	&__title {
		margin-bottom: 30px; } }




