html {
	min-height: 100%; }

body {
	min-width: 1280px;

	background: #030f15 url('../img/bg2x.png') top center / 100%;
	@include reg;
	font-size: 14px;
	line-height: 1.1;
	color: #fff;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	&.is-blured {
		.page {
			filter: blur(70px); } } }

.page {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	transition: filter .3s; }

.out {
	position: relative;
	flex: 1 0 auto;
	&.is-login {
		display: flex; } }

.slick-arrow {
	width: 35px;
	height: 35px;
	font-size: 0; }

.slick-prev {
	background-image: url(data:image/svg+xml;base64,);
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	background-color: transparent; }

.slick-next {
	background-image: url(data:image/svg+xml;base64,);
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	background-color: transparent; }

.no-scroll {
	overflow: hidden; }
