.s-form {
	position: relative;
	&__input {
		height: 44px;
		border-radius: 4px;
		width: 100%;
		background: #0f181d;
		padding: 5px 55px;
		@include pop;
		font-size: 14px;
		color: #fff;
		caret-color: $c-blue;
		&:placeholder {
			color: rgba(#fff, .6); } }
	&__icon {
		position: absolute;
		top: 50%;
		left: 28px;
		margin-top: -8px;
		font-size: 0;
		opacity: .6;
		.icon {
			font-size: 16px; } }
	.icon {
		fill: #fff; }
	&__close {
		position: absolute;
		right: 20px;
		top: 0;
		bottom: 0;
		font-size: 0;
		background: none;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		.icon {
			font-size: 16px; } } }

.input {
	$this: &;

	position: relative;
	padding-top: 10px;
	@include pop;
	&__main {
		width: 100%;
		height: 35px;
		padding-bottom: 10px;
		background: none;
		caret-color: $c-blue;
		border-bottom: 1px solid #9a9fa1;
		color: #fff;
		font-size: 14px;
		transition: border .3s;
		&[type*="password"] {
			letter-spacing: 8px; }
		&:read-only {
			border-color: transparent;
			pointer-events: none; }
		&.is-filled {
			~ #{$this}__placeholder {
				transform: translateY(-20px); } }
		&:focus {
			border-color: $c-blue;
			~ #{$this}__placeholder {
				transform: translateY(-20px); } } }
	&__placeholder {
		position: absolute;
		text-transform: uppercase;
		font-weight: 300;
		color: #9fa2a5;
		bottom: 13px;
		pointer-events: none;
		transition: transform .3s; } }

.input_lg {
	.input__main {
		font-size: 16px; } }

.input_mod {
	.input {
		&__main {
			padding-bottom: 0; }
		&__placeholder {
			bottom: 9px; } } }

.input_simple {
	padding-top: 0;
	.input {
		&__main {
			padding-bottom: 0;
			height: 60px; } } }

.checkbox {
	display: inline-block;
	vertical-align: top;
	cursor: pointer;
	user-select: none;
	input {
		position: absolute;
		opacity: 0;
		visibility: hidden;
		&:checked {
			~ span {
				&:after {
					opacity: 1; } } } }
	span {
		position: relative;
		padding-left: 27px;
		padding-top: 2px;
		color: #9da0a3;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 16px;
			height: 16px;
			border: 1px solid #fff;
			border-radius: 2px; }
		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 16px;
			height: 16px;
			background-image: url(data:image/svg+xml;base64,);
			background-size: 7px 6px;
			background-position: center center;
			background-repeat: no-repeat;
			z-index: 2;
			opacity: 0;
			transition: opacity .2s; } } }

.c-checkbox {
	display: inline-block;
	vertical-align: top;
	cursor: pointer;
	input {
		position: absolute;
		visibility: hidden;
		opacity: 0;
		&:checked {
			~ span {
				background: $c-blue;
				border-color: $c-blue;
				i {
					transform: translateX(20px); } } } }
	span {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 42px;
		height: 22px;
		border: 1px solid #E5E5E5;
		background: #000;
		border-radius: 18px;
		transition: background .3s, border .3s;
		i {
			position: absolute;
			width: 20px;
			height: 20px;
			top: 0px;
			left: 0px;
			background: #fff;
			box-shadow: 0 5px 10px 0 rgba(0,0,0,0.10), 0 2px 2px 0 rgba(0,0,0,0.30);
			border-radius: 50%;
			transition: transform .3s; } } }





