.container {
	position: relative;
	max-width: 1230px;
	margin: 0 auto;
	padding: 0 15px;
	@include clr; }

.container-sm {
	position: relative;
	max-width: 530px;
	margin: 0 auto;
	padding: 0 15px;
	@include clr; }

.row {
	@include clr;
	margin: 0 -15px;
	> div {
		float: left;
		padding: 0 15px 50px; } }

.row_mb {
	margin-bottom: -50px; }

.row_inline {
	font-size: 0;
	> div {
		float: none;
		display: inline-block;
		vertical-align: top;
		font-size: 14px; } }

.col-4 {
	width: 25%; }
